<template>
  <div class="container">
    <form action>
      <h1 class="title"></h1>
      <p>
        <label for="uname">:</label>
        <el-input
          placeholder=""
          id="uname"
          v-model="uname"
          class="input"
          required
          @blur="checkname"
        ></el-input>
        <span v-text="nameMsg" style="color: red"></span>
      </p>
      <p>
        <label for="password">:</label>
        <el-input
          placeholder=""
          show-password
          v-model="password"
          class="input"
          id="password"
          @blur="checkPass"
        ></el-input>
        <span v-text="passwordMsg" style="color: red"></span>
      </p>
      <p>
        <label for="confirm">:</label>
        <el-input
          placeholder="8-16"
          show-password
          v-model="confirm"
          class="input"
          id="confirm"
          @blur="checkPassword"
        ></el-input>
        <span v-if="errorText === ''" class="error">
          <i class="iconfont icon-shibai"></i>
          {{errorText}}
        </span>
        <span v-if="successText === 'ok'" class="success">
          <i class="iconfont icon-chenggong"></i>
          {{successText}}
        </span>
      </p>
      <p class="checking">
        <label for="phone">:</label>
        <el-input placeholder="" v-model="phone" id="phone" class="input"></el-input>
        <el-button type="primary" class="getcode" @click="getCode"></el-button>
      </p>
      <p>
        <label for="code">:</label>
        <el-input placeholder="" v-model="code" class="input" id="code"></el-input>
      </p>
      <p class="btns">
        <el-button type="primary" class="btn" @click="register"></el-button>
        <el-button type="info" @click="reset" class="btn"></el-button>
      </p>
      <p class="link">
        ?
        <router-link to="/minlogin" class="create"></router-link>
      </p>
    </form>
  </div>
</template>

<script>
// import $ from 'jquery'
export default {
  data () {
    return {
      uname: '', // 
      password: '', // 
      confirm: '', // 
      phone: '', // 
      code: '', // 
      realCode: '', // 
      errorText: '', // 
      successText: '', // 
      nameMsg: '',
      passwordMsg: ''
    }
  },
  components: {

  },
  methods: {
    // 
    checkname () {
      if (this.uname.trim().length < 2) {
        this.nameMsg = ''
      } else if (this.uname.trim.length > 16) {
        this.nameMsg = ''
      } else {
        this.nameMsg = ''
      }
    },
    // 
    checkPass () {
      if (this.password.trim().length < 6) {
        this.passwordMsg = '6'
      } else if (this.password.trim().length > 16) {
        this.passwordMsg = '16'
      } else {
        this.passwordMsg = ''
      }
    },
    // 
    reset () {
      this.uname = ''
      this.password = ''
      this.confirm = ''
      this.phone = ''
      this.code = ''
    },
    // 
    checkPassword () {
      if (this.confirm !== this.password) {
        this.errorText = ''
        this.successText = ''
      } else {
        this.successText = 'ok'
        this.errorText = ''
      }
      if (this.confirm === '') {
        this.errorText = ''
        this.successText = ''
      }
    },
    // 
    getCode () {
      this.axios.post('/phone/getotp?userPhone=' + this.phone)
        .then(response => {
          var code = response.data.data.otpCode
          this.realCode = code
          this.$alert(code, '', {
            confirmButtonText: ''
          })
        })
        .catch(error => {
          console.log(error)
          this.$message.error('')
        })
    },
    // 
    register () {
      // 
      if (this.uname.trim().length === 0) {
        this.nameMsg = ''
        return
      }
      if (this.password.trim().length === 0) {
        this.passwordMsg = ''
        return
      }
      if (this.confirm.trim() !== this.password.trim() || this.phone.trim().length === 0 || this.code.trim().length === 0) {
        this.$message.error('')
        return
      }
      // 
      var formData = new URLSearchParams()
      formData.append('userPhone', this.phone.trim())
      formData.append('password', this.password.trim())
      formData.append('username', this.uname.trim())
      formData.append('otp', this.code.trim())
      this.axios.post('/register', formData, { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } }, { withCredentials: true })
        .then(res => {
          if (res.data.status === 0) {
            this.$message({
              message: '',
              type: 'success'
            })
            this.$router.push('/minlogin')
          } else {
            this.$message.error(res.data.msg)
          }
        })
        .catch(err => {
          this.$message.error(err)
        })
    }
  }
}
</script>

<style scoped lang="less">
.container {
  position: relative;
  height: 100%;
  background: url('../assets/image/background.jpg') no-repeat;
  background-position: center center;
}
form {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 468px;
  height: 544px;
  padding: 0 30px;
  margin-top: -272px;
  margin-left: -234px;
  border-radius: 10px;
  background-color: #fff;
  p {
    margin-top: 30px;
    span {
      font-size: 13px;
      padding-left: 8px;
    }
    .success {
      color: #88d260;
    }
    .error {
      color: #e84335;
    }
    label {
      display: inline-block;
      width: 85px;
      text-align: right;
      margin-right: 15px;
      font-size: 19px;
      font-weight: 700;
    }
  }
}
.title {
  height: 60px;
  padding: 0 20px;
  line-height: 70px;
  color: #409eff;
  text-align: center;
}
.checking .input {
  width: 200px;
  margin-right: 20px;
}
.input {
  width: 200px;
}
.btns {
  text-align: center;
  margin-left: 15px;
  .btn {
    margin-right: 30px;
  }
}
.create {
  margin-top: 30px;
  color: #409eff;
  text-decoration: none;
}
.link {
  text-align: right;
  margin-right: 20px;
}
</style>
