<template>
  <div class="content">
    <p class="checkbox">
      <label for="phone">:</label>
      <el-input placeholder="" v-model="id" class="input"></el-input>
      <el-button type="primary" class="code" @click="getCode"></el-button>
    </p>
    <p>
      <label for="code">:</label>
      <el-input
        placeholder=""
        v-model="code"
        class="input"
        id="code"
        @keydown.enter.native="login"
      ></el-input>
    </p>
    <p class="btns">
      <el-button type="primary" class="btn" @click="login"></el-button>
      <el-button type="info" @click="reset" class="btn"></el-button>
    </p>
    <p class="link">
      ?
      <router-link to="/register" class="create"></router-link>
    </p>C
  </div>
</template>

<script>
export default {
  data () {
    return {
      id: '', // 
      code: '' // 
    }
  },
  components: {

  },
  methods: {
    // 
    reset () {
      this.id = ''
      this.code = ''
    },
    // 
    getCode () {
      this.axios.post('/phone/getotp?userPhone=' + this.id)
        .then(response => {
          var code = response.data.data.otpCode
          this.$alert(code, '', {
            confirmButtonText: ''
          })
        })
        .catch(error => {
          console.log(error)
          this.$message.error('')
        })
    },
    // 
    login () {
      if (this.id === '' || this.code === '') {
        this.$message.error('')
        return
      }
      // 
      var formData = new FormData()
      formData.append('userPhone', this.id.trim())
      formData.append('otp', this.code.trim())
      // 
      this.axios.post('/login/phone', formData)
        .then(res => {
          if (res.data.status === 0) {
            this.$message({
              message: '',
              type: 'success'
            })
            // 
            window.sessionStorage.setItem('userId', res.data.data)
            window.sessionStorage.setItem('login', true)
            // 
            this.$router.push('/index')
          } else {
            this.$message.error(res.data.msg)
          }
        })
        .catch(error => {
          console.log(error)
          this.$message.error('')
        })
    }
  }
}
</script>

<style scoped lang="less">
.content {
  width: 500px;
  height: 450px;
  padding: 10px 60px 10px 100px;
  margin-top: 30px;
  p {
    margin-top: 40px;
    label {
      display: inline-block;
      width: 65px;
      text-align: right;
      font-size: 19px;
      font-weight: 700;
      color: #666;
    }
  }
}
.input {
  width: 300px;
  margin-left: 15px;
}
.btns {
  text-align: center;
  margin-right: 36px;
  .btn {
    margin-right: 30px;
  }
}
.checkbox .input {
  width: 167px;
  margin-right: 20px;
}
.create {
  margin-top: 30px;
  color: #409eff;
  text-decoration: none;
}
.link {
  text-align: right;
  margin-right: 20px;
}
</style>
