<template>
  <!--  -->
  <div class="main clearfix">
    <div class="menu">
      <ul>
        <li>
          <router-link to="/master/message"></router-link>
        </li>
        <li>
          <router-link to="/master/edit"></router-link>
        </li>
        <li>
          <router-link to="/master/editBlog"></router-link>
        </li>
        <li>
          <router-link to="/master/collection"></router-link>
        </li>
        <li>
          <router-link to="/master/editPassword"></router-link>
        </li>
        <li>
          <router-link to="/master/phoneEditPassword"></router-link>
        </li>
        <li>
          <a href="#" @click.prevent="logout"></a>
        </li>
      </ul>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data () {
    return {

    }
  },
  components: {

  },
  methods: {
    // 
    logout () {
      this.axios.get('/loginout')
        .then(res => {
          // 
          if (res.data.success) {
            // session
            window.sessionStorage.setItem('login', false)
            window.sessionStorage.setItem('userId', null)
            window.sessionStorage.setItem('user', null)
            this.$message({
              message: '',
              type: 'success'
            })
            // 
            this.$router.push('/minlogin')
          } else {
            this.$message.error('')
          }
        })
        .catch(err => {
          console.log(err)
          this.$message.error('')
        })
    }
  }
}
</script>

<style scoped lang="less">
a {
  text-decoration: none;
}
li {
  list-style: none;
}
.main {
  width: 1200px;
  margin: 0 auto;
  margin-top: 62px;
  .menu {
    float: left;
    width: 230px;
    background-color: #fff;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
    ul {
      li {
        a {
          display: inline-block;
          width: 100%;
          padding: 15px 0 15px 0;
          text-indent: 1em;
          color: black;
        }
        .router-link-active {
          color: white;
          background-color: #409eff;
        }
      }
    }
  }
}
</style>
